













































































































interface SearchValue {
  page: string;
  cate: string;
  deadline: string;
}
import tableMixin from "@/mixins/table";
import { AddForm, EditForm } from "./components";
import { Component, Vue, Mixins } from "vue-property-decorator";

@Component({
  components: {
    EditForm,
    AddForm
  }
})
export default class Plan extends Mixins(tableMixin) {
  searchConditions: SearchValue = {
    page: "feature",
    cate: "",
    deadline: ""
  };

  handleDelete(index, row) {
    console.log(index, row);
  }

  handleSearch() {
    this.getList(this._getList);
  }

  async _getList() {
    let {page,...data}=this.searchConditions
    let query=this.$lodash.getSearchQueryData(data)
    console.log(query)
    return await this.$API.featureModel.GetList(query);
  }

  async addForm() {
    let d = await (this as any).$refs.addForm._saveData();
    this.isShowAddDialog = false;
    if (d.code == 0) {
      this.getList(this._getList);
    }
  }

  //分页
  handleSizeChange(val) {
    this.pagination.pagesize = val;
  }

  handleCurrentChange(val) {
    this.pagination.currentPage = val;
    let { currentPage, pagesize } = this.pagination;
    let query = {
      currentPage,
      pagesize
    };
    this.getList(this._getList);
  }

  handleRefresh(){
    this.getList(this._getList,"refresh");
  }

  created() {
    this.getList(this._getList);
  }
}
